import './home.css'

function TextHome(){
    return(
        <div className='TextHome'>
            <h1>
                UPA - UNIO PRESBITERIANA DE ADOLESCENTES
            </h1>
            <p>
            A Unio Presbiteriana de Adolexcentes (UPA)  uma sociedade interna da Igreja Persbiteriana dedicada ao trabalho com 
            adolescentes de 12 a 18 anos. Tem como objetivo promover a integrao e o desenvolvimento pessoal e espiritual de adolescentes 
            dentro do contexto da IPB (Igreja Presbiteriana do Brasil).
            <br></br>
            <br></br>
            Atravs de encontros e atividades educacionais, sociais e religiosas, a UPA busca proporcionar aos jovens um ambiente seguro e 
            acolhedor para que possam crescer em sua f e em sua identidade como cidados comprometidos em seguir a palavra de Deus. 
            Principalmente como cristos.
            </p>
        </div>
    )
}

export default function Home(){
    return(
        <section className="home">
            <TextHome/>
        </section>
    )
}